import React from 'react';
import './css/app.css';
import './css/fonts.css';
import './css/svg.css';
import './css/form.css';
import './css/table.css';
import 'react-datepicker/dist/react-datepicker.css';

import LoadingPage from './components/static/loading';
import NotFoundPage from './components/static/notfound';
import ProfilePage from './components/profile/profile';
import LoginPage from './components/login/login';
import SignUpPage from './components/signup/signup';

export default class App extends React.Component {

  constructor(props) {
    super(props);

    this.props.autologin();
  }

  render() {
    if (this.props.security.loggedIn === null) {
      return <LoadingPage />;
    }
    if (!this.props.security.loggedIn) {
      if (this.props.path == "/signup") {
        return <SignUpPage
          error={this.props.security.signUpError}
          signUp={this.props.signUp}
          clearError={this.props.clearSignUpError}
        />;
      }
      return <LoginPage
        cookies={this.props.security.cookies}
        error={this.props.security.loginError}
        login={this.props.login}
        changeLocation={this.props.changeLocation}
        clearError={this.props.clearLoginError}
      />
    }
    switch (this.props.path) {
      case "":
      case "/":
        return <ProfilePage
          path="/profile"
          logout={this.props.logout}
          user={this.props.security.user}
          formState={this.props.profile.formState}
          formStateError={this.props.profile.formStateError}
          orders={this.props.profile.orders}
          ordersError={this.props.profile.ordersError}
          ordersLoading={this.props.profile.ordersLoading}
          saveProfile={this.props.saveProfile}
          addOrder={this.props.addOrder}
          cancelOrder={this.props.cancelOrder}
        />;
      case "/profile":
        return <ProfilePage
          path={this.props.path}
          logout={this.props.logout}
          user={this.props.security.user}
          formState={this.props.profile.formState}
          formStateError={this.props.profile.formStateError}
          orders={this.props.profile.orders}
          ordersError={this.props.profile.ordersError}
          ordersLoading={this.props.profile.ordersLoading}
          saveProfile={this.props.saveProfile}
          addOrder={this.props.addOrder}
          cancelOrder={this.props.cancelOrder}
        />;
      case "/signup":
        return <SignUpPage
          error={this.props.security.signUpError}
          signUp={this.props.signUp}
          clearError={this.props.clearSignUpError}
        />;
      default:
        return <NotFoundPage
          path={this.props.path}
          changeLocation={this.props.changeLocation}
        />;
    }
  }
};

