import React from 'react';
import Row from '../../form/row';
import Button from '../../button/button';
import Cell from '../../form/cell';
import Form from '../../form/form';
import Input from '../../input/input';
import CheckboxInput from '../../input/checkbox-input';
import DateInput from '../../input/date';
import { validateEmail, validatePassword, validateMandatory, formatPhone } from '../../../services/validation';
import PasswordInput from '../../input/password';
import { FORM_STATE } from '../../../constants/profile';

export default class ProfileTab extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = this.getPropsValues();

        this.changePhoto = this.changePhoto.bind(this);
        this.changeInput = this.changeInput.bind(this);
        this.changeBirthday = this.changeBirthday.bind(this);
        this.changeSex = this.changeSex.bind(this);
        this.changeEmail = this.changeEmail.bind(this);
        this.changePhone = this.changePhone.bind(this);
        this.changePassword = this.changePassword.bind(this);
        this.changePasswordConfirm = this.changePasswordConfirm.bind(this);
        this.startEdit = this.startEdit.bind(this);
        this.save = this.save.bind(this);
        this.cancelEdit = this.cancelEdit.bind(this);
    }

    getPropsValues() {
        return {
            locked: true,
            photo: this.props.photo || null,
            surname: this.props.surname || "",
            name: this.props.name || "",
            midname: this.props.midname || "",
            birthday: this.props.birthday || new Date(),
            sex: this.props.sex || 0,
            email: this.props.email || "",
            phone: this.props.phone || "",
            password: "*****",
            passwordConfirm: "*****",
            validation: {
                surname: this.props.surname ? validateMandatory(this.props.surname) : null,
                name: this.props.name ? validateMandatory(this.props.name) : null,
                midname: this.props.midname ? validateMandatory(this.props.midname) : null,
                birthday: this.props.birthday ? validateMandatory(this.props.birthday) : true,
                email: this.props.email ? validateEmail(this.props.email) : null,
                phone: this.props.phone ? this.validatePhone(this.props.phone) : true,
                password: null,
                passwordConfirm: null,
            }
        }
    }

    changePhoto() {
        const profile = this;
        const input = document.createElement("input");
        input.type = "file";
        input.accept = "image/*";
        input.addEventListener("change", function (event) {
            const file = input.files[0];
            const reader = new FileReader();

            reader.addEventListener("load", function () {
                // convert image file to base64 string
                profile.setState(Object.assign({}, profile.state, { photo: reader.result }));
            }, false);

            if (file) {
                reader.readAsDataURL(file);
            }
        });
        input.click();
    }

    changeInput(parameter, mandatory, value) {
        const nextState = Object.assign({}, this.state);
        nextState[parameter] = value;
        if (mandatory) {
            nextState.validation[parameter] = validateMandatory(value);
        }
        this.setState(nextState);
    }

    changeBirthday(value) {
        const nextState = Object.assign({}, this.state);
        nextState.birthday = value;
        nextState.validation.birthday = validateMandatory(value);
        this.setState(nextState);
    }

    changeSex(value) {
        this.setState(Object.assign({}, this.state, { sex: value }));
    }

    changeEmail(value) {
        const nextState = Object.assign({}, this.state);
        nextState.email = value;
        nextState.validation.email = validateEmail(value);
        this.setState(nextState);
    }


    changePhone(value) {
        value = value.replace(/[^0-9]/g, "");
        const nextState = Object.assign({}, this.state);
        nextState.phone = formatPhone(value);
        nextState.validation.phone = this.validatePhone(value)
        this.setState(nextState);
    }

    changePassword(value) {
        const nextState = Object.assign({}, this.state);
        nextState.password = value;
        nextState.validation.password = validatePassword(value);
        nextState.validation.passwordConfirm = this.validatePasswordConfirm(this.state.passwordConfirm, value);
        this.setState(nextState);
    }

    changePasswordConfirm(value) {
        const nextState = Object.assign({}, this.state);
        nextState.passwordConfirm = value;
        nextState.validation.passwordConfirm = this.validatePasswordConfirm(value);
        this.setState(nextState);
    }

    validatePhone(value) {
        value = value.replace(/[^0-9]/g, "");
        return value.length == 11 || value.length == 0;
    }

    validatePasswordConfirm(value, password = this.state.password) {
        return value === password;
    }

    isValid() {
        return this.state.validation.surname
            && this.state.validation.name
            && this.state.validation.midname
            && this.state.validation.birthday
            && this.state.validation.email
            && this.state.validation.phone
            && this.state.validation.password != false
            && this.state.validation.passwordConfirm != false;
    }

    startEdit() {
        this.setState(Object.assign({}, this.state, { locked: false }));
    }

    save() {
        let changes = {
            login: this.props.email,
            password: this.state.password
        };
        if (this.state.photo != this.props.photo) {
            changes.photo = this.state.photo;
        }
        if (this.state.surname != this.props.surname) {
            changes.surname = this.state.surname;
        }
        if (this.state.name != this.props.name) {
            changes.name = this.state.name;
        }
        if (this.state.midname != this.props.midname) {
            changes.midname = this.state.midname;
        }
        if (this.state.birthday != this.props.birthday) {
            changes.birthday = this.state.birthday;
        }
        if (this.state.sex != this.props.sex) {
            changes.sex = this.state.sex;
        }
        if (this.state.email != this.props.email) {
            changes.email = this.state.email;
        }
        if (this.state.phone != this.props.phone) {
            changes.phone = this.state.phone;
        }
        this.props.save(changes);
    }

    cancelEdit() {
        this.setState(Object.assign({}, this.state, this.getPropsValues()));
    }

    componentDidUpdate(prevProps) {
        if (prevProps.formState == FORM_STATE.WAIT && this.props.formState == FORM_STATE.READY) {
            this.cancelEdit();
        }
    }

    render() {
        return <div className="tab-body">
            <Row className="justify-right">
                <Button
                    className={"btn-blue btn-edit" + (this.state.locked ? "" : " invisible")}
                    icon="svg icon-edit"
                    label=""
                    onClick={this.startEdit}
                />
            </Row>
            <Row className="body-form">
                <Cell className="justify-start align-center photo-cell">
                    <Photo image={this.state.photo} />
                    {!this.state.locked && <Row>
                        <Cell>
                            <Button
                                label=""
                                className="btn-blue"
                                onClick={this.changePhoto}
                            />
                        </Cell>
                    </Row>}
                </Cell>
                <Cell className="justify-start align-center">
                    <Form>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder=""
                                    password={false}
                                    value={this.state.surname}
                                    valid={this.state.validation.surname}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changeInput.bind(this, "surname", true)}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder=""
                                    password={false}
                                    value={this.state.name}
                                    valid={this.state.validation.name}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changeInput.bind(this, "name", true)}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder=""
                                    password={false}
                                    value={this.state.midname}
                                    valid={this.state.validation.midname}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changeInput.bind(this, "midname", true)}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <DateInput
                                    label=" "
                                    placeholder=""
                                    password={false}
                                    value={this.state.birthday}
                                    valid={this.state.validation.birthday}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changeBirthday}
                                />
                            </Cell>
                            <div className="filler" />
                            <Cell>
                                <CheckboxInput
                                    label=""
                                    placeholder=""
                                    password={false}
                                    value={this.state.sex}
                                    locked={this.state.locked}
                                    keys={[1, 0]}
                                    shortValueMap={{ 0: "", 1: "" }}
                                    fullValueMap={{ 0: "", 1: "" }}
                                    onChange={this.changeSex}
                                />
                            </Cell>
                        </Row>
                    </Form>
                </Cell>
                <Cell className="justify-start align-center">
                    <Form>
                        <Row>
                            <Cell>
                                <Input
                                    label="Email"
                                    placeholder=""
                                    password={false}
                                    value={this.state.email}
                                    valid={this.state.validation.email}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changeEmail}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder="  ( 9 999-999-9999)"
                                    password={false}
                                    value={this.state.phone}
                                    valid={this.state.validation.phone}
                                    locked={this.state.locked}
                                    mandatory={false}
                                    onChange={this.changePhone}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <PasswordInput
                                    label=""
                                    placeholder=""
                                    password={true}
                                    value={this.state.password}
                                    valid={this.state.validation.password}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changePassword}
                                />
                            </Cell>
                        </Row>
                        {!this.state.locked && <Row>
                            <Cell>
                                <Input
                                    label=" "
                                    placeholder=""
                                    password={true}
                                    value={this.state.passwordConfirm}
                                    valid={this.state.validation.passwordConfirm}
                                    locked={this.state.locked}
                                    mandatory={true}
                                    onChange={this.changePasswordConfirm}
                                />
                            </Cell>
                        </Row>}
                    </Form>
                </Cell>
            </Row>
            {!this.state.locked && <Row className="justify-right align-center edit-btns">
                {this.props.formState == FORM_STATE.SAVE_ERROR && <div className="note-danger">{this.props.formStateError || "  "}</div>}
                <Button
                    className="btn-green"
                    label=""
                    disabled={!this.isValid()}
                    onClick={this.save}
                />
                <Button
                    className="btn-blue btn-edit"
                    label=""
                    onClick={this.cancelEdit}
                />
            </Row>}
        </div>;
    }
}

class Photo extends React.PureComponent {
    render() {
        if (!this.props.image) {
            return <div className="profile-photo svg icon-empty-profile"></div>;
        }
        return <div className="profile-photo">
            <img src={this.props.image} />
        </div>;
    }
}