import React from 'react';

export default class LockedInput extends React.PureComponent {

    render() {
        let className = "form-input locked-input align-center";
        if (this.props.valid === false) {
            className += " invalid";
        }
        if(this.props.short){
            className += " short";
        }
        let value = this.props.value;
        if (this.props.password) {
            value = value.replace(/./g, "*");
        }
        return [
            this.props.label ? <span key="label" className="input-label">{this.props.label}</span> : null,
            <div key="input" className={className}>
                {value}
            </div>
        ]
    }
}