import React from 'react';
import Checkbox from './checkbox';
import LockedInput from './locked';

export default class CheckboxInput extends React.PureComponent {
    render() {
        if (this.props.locked) {
            return <LockedInput {...this.props} value={this.props.fullValueMap[this.props.value]} short={true} />
        }
        let className = "form-input input input-text";
        if (this.props.valid === false) {
            className += " invalid";
        }
        let label = this.props.label;
        if(this.props.mandatory){
            label += " *";
        }
        return [
            this.props.label ? <span key="label" className="input-label">{label}</span> : null,
            <div key="checkbox" className="checkbox-input-container">
                {this.props.keys.map((k) => <Checkbox
                    key={k}
                    value={this.props.value == k}
                    label={this.props.shortValueMap[k]}
                    onChange={this.props.onChange.bind(this, k)}
                />)}
            </div>
        ]
    }
}