import { SECURITY_SEND_LOGIN_ERROR, SECURITY_SEND_LOGIN_INFO, SIGN_UP_ERROR, SECURITY_SEND_SIGN_UP_ERROR, SECURITY_SEND_SIGN_UP_INFO, SECURITY_SEND_LOGOUT, SECURITY_SEND_COOKIES, SECURITY } from "../constants/security";
import { changeLocation } from "./location";
import { setCookie, uuidv4, parseUserFromServer } from "../services/security";
import { getOrders } from "./profile";

/*******************
 * Utils functions *
 *******************/
function checkUuid(dispatch, cookies) {
    if (cookies.uuid) {
        return;
    }
    const expireDate = new Date();
    expireDate.setMinutes(expireDate.getMinutes() + 30);
    const uuid = uuidv4();
    setCookie("uuid", uuid, expireDate);
    dispatch(sendCookies({ uuid: uuid }));
}

/******************
 * Ajax functions *
 ******************/
async function ajaxGetLoginInfo(dispatch) {
    let response = await fetch('/prepare.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify({})
    });

    if (!response.ok) {
        dispatch(sendLogout());
        return;
    }
    let result = null;
    try {
        result = await response.json();
    } catch (e) {
        dispatch(sendLogout());
        return;
    }
    if (!result) {
        dispatch(sendLogout());
        return;
    }
    dispatch(sendLoginInfo(parseUserFromServer(result)));
    dispatch(getOrders());
}

async function ajaxSendLoginInfo(dispatch, user, password, remember) {
    let response = await fetch('/login.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify({ user, password })
    });

    if (!response.ok) {
        dispatch(sendLoginError());
        return;
    }
    let result = null;
    try {
        result = await response.json();
    } catch (e) {
        dispatch(sendLoginError());
        return;
    }
    if (!result) {
        dispatch(sendLoginError());
        return;
    }
    if (result.errorMessage) {
        dispatch(sendLoginError({ errorCode: result.errorCode, errorMessage: result.errorMessage }));
        return;
    }
    dispatch(sendLoginInfo(parseUserFromServer(result)));
    dispatch(changeLocation("/"));
    if (remember) {
        const expireDate = new Date();
        expireDate.setMonth(expireDate.getMonth() + 1);
        setCookie("user", user, expireDate);
    }
    dispatch(getOrders());
}

async function ajaxSendlogout(dispatch) {
    await fetch('/logout.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        }
    });

    dispatch(sendLogout());
    dispatch(changeLocation("/"));
    setCookie("uuid", "", new Date(Date.now() - 1000));
}

async function ajaxSignUp(dispatch, surname, name, midname, email, password) {
    let response = await fetch('/signup.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify({ surname, name, midname, email, password })
    });

    if (!response.ok) {
        dispatch(sendSignUpError({ errorCode: SIGN_UP_ERROR.SERVER }));
        return;
    }
    let result = null;
    try {
        result = await response.json();
    } catch (e) {
        dispatch(sendSignUpError({ errorCode: SIGN_UP_ERROR.PARSE }));
        return;
    }
    if (!result) {
        dispatch(sendSignUpError({ errorCode: SIGN_UP_ERROR.SERVER }));
    }
    if (result.errorMessage) {
        dispatch(sendSignUpError({ errorCode: SIGN_UP_ERROR.ERROR_WITH_MESSAGE, errorMessage: result.errorMessage }));
        return;
    }
    dispatch(sendSignUpInfo(result));
    dispatch(changeLocation("/"));
    dispatch(getOrders());
}

/*****************
 * Plain actions *
 *****************/
export function sendLoginError(error = true) {
    return {
        type: SECURITY_SEND_LOGIN_ERROR,
        payload: {
            error: error
        }
    }
}

export function sendLoginInfo(user) {
    return {
        type: SECURITY_SEND_LOGIN_INFO,
        payload: {
            user: user
        }
    }
}

export function sendLogout() {
    return {
        type: SECURITY_SEND_LOGOUT,
        payload: null
    }
}

export function sendSignUpError(error) {
    return {
        type: SECURITY_SEND_SIGN_UP_ERROR,
        payload: {
            error: error
        }
    }
}

export function sendSignUpInfo(user) {
    return {
        type: SECURITY_SEND_SIGN_UP_INFO,
        payload: {
            user: user
        }
    }
}

export function sendCookies(cookies) {
    return {
        type: SECURITY_SEND_COOKIES,
        payload: {
            cookies: cookies
        }
    }
}

/***********
 * Actions *
 ***********/
export function autologin() {
    return async function (dispatch, getState) {
        if (!getState()[SECURITY].cookies.uuid) {
            dispatch(sendLogout());
            return;
        }
        ajaxGetLoginInfo(dispatch);
    }
}

export function login(user, password, remember) {
    return async function (dispatch, getState) {
        checkUuid(dispatch, getState()[SECURITY].cookies);
        ajaxSendLoginInfo(dispatch, user, password, remember);
    }
}

export function logout() {
    return function (dispatch, getState) {
        ajaxSendlogout(dispatch);
    }
}

export function signUp(surname, name, midname, email, password) {
    return async function (dispatch, getState) {
        checkUuid(dispatch, getState()[SECURITY].cookies);
        ajaxSignUp(dispatch, surname, name, midname, email, password);
    }
}