export const emailRegExp = new RegExp(/.+@.+\..+/);
export const uppercaseSymbolRegExp = new RegExp(/.*[A-Z-].*/);
export const numberRegExp = new RegExp(/.*[0-9].*/);

export function validateEmail(value) {
    return emailRegExp.test(value);
}

export function validateMandatory(value) {
    return value.length != 0;
}

export function validatePassword(value) {
    return validatePasswordLength(value) && validatePasswordNumber(value) && validatePasswordUppercase(value);
}

export function validatePasswordLength(value) {
    return value.length >= 6;
}

export function validatePasswordNumber(value) {
    return numberRegExp.test(value);
}

export function validatePasswordUppercase(value) {
    return uppercaseSymbolRegExp.test(value);
}

export function formatPhone(value) {
    let i = 0,
        v = value.replace(/[^0-9]/g, "").toString();
    return "# ###-###-####".replace(/#/g, _ => {
        const c = v[i++];
        if (typeof c == "undefined") {
            return "#";
        }
        return c;
    }).replace(/#.*/, "").replace(/[- ]$/, "");
}