import { parseUserFromServer, parseUserToServer } from "../services/security";
import { PROFILE_SEND_SAVE_PROFILE_WAIT, PROFILE_SEND_SAVE_PROFILE_ERROR, PROFILE_SEND_SAVE_PROFILE_SUCCESS, PROFILE_SEND_GET_ORDERS_WAIT, PROFILE_SEND_GET_ORDERS_ERROR, PROFILE_SEND_GET_ORDERS_SUCCESS } from "../constants/profile";
import { parseOrdersFromServer, parseOrderToServer } from "../services/profile";
import { SECURITY } from "../constants/security";

/******************
 * Ajax functions *
 ******************/
async function ajaxSaveProfile(dispatch, login, photo, surname, name, midname, birthday, sex, email, phone, password) {
    dispatch(sendSaveProfileWait());
    let response = await fetch('/saveprofile.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(parseUserToServer({ photo, login, surname, name, midname, birthday, sex, email, phone, password }))
    });

    if (!response.ok) {
        dispatch(sendSaveProfileError());
        return;
    }
    let result = null;
    try {
        result = await response.json();
    } catch (e) {
        dispatch(sendSaveProfileError());
        return;
    }
    if (!result) {
        dispatch(sendSaveProfileError());
        return;
    }
    if (result.errorMessage) {
        dispatch(sendSaveProfileError(result.errorMessage));
        return;
    }
    dispatch(sendSaveProfileSuccess(parseUserFromServer(result)));
}

export async function ajaxGetOrders(dispatch, login) {
    dispatch(sendGetOrdersWait());
    let response = await fetch('/getorders.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify({ login: login })
    });

    if (!response.ok) {
        dispatch(sendGetOrdersError());
        return;
    }
    let result = null;
    try {
        result = await response.json();
    } catch (e) {
        dispatch(sendGetOrdersError());
        return;
    }
    if (!result) {
        dispatch(sendGetOrdersError());
        return;
    }
    if (result.errorMessage) {
        dispatch(sendGetOrdersError(result.errorMessage));
        return;
    }
    dispatch(sendGetOrdersSuccess(parseOrdersFromServer(result)));
}

export async function ajaxAddOrder(dispatch, sourcePlace, destinationPlace, vehicle) {
    await fetch('/addorder.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(parseOrderToServer({ sourcePlace, destinationPlace, vehicle }))
    });
    dispatch(getOrders());
}

export async function ajaxCancelOrder(dispatch, number) {
    await fetch('/cancelorder.php', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(parseOrderToServer({ number }))
    });
    dispatch(getOrders());
}

/*****************
 * Plain actions *
 *****************/
export function sendSaveProfileWait() {
    return {
        type: PROFILE_SEND_SAVE_PROFILE_WAIT,
        payload: null
    }
}

export function sendSaveProfileError(errorMessage = null) {
    return {
        type: PROFILE_SEND_SAVE_PROFILE_ERROR,
        payload: {
            errorMessage
        }
    }
}

export function sendSaveProfileSuccess(user) {
    return {
        type: PROFILE_SEND_SAVE_PROFILE_SUCCESS,
        payload: {
            user: user
        }
    }
}

export function sendGetOrdersWait() {
    return {
        type: PROFILE_SEND_GET_ORDERS_WAIT,
        payload: null
    }
}

export function sendGetOrdersError(error = true) {
    return {
        type: PROFILE_SEND_GET_ORDERS_ERROR,
        payload: {
            error: error
        }
    }
}

export function sendGetOrdersSuccess(orders) {
    return {
        type: PROFILE_SEND_GET_ORDERS_SUCCESS,
        payload: {
            orders: orders
        }
    }
}

/***********
 * Actions *
 ***********/
export function saveProfile(login, photo, surname, name, midname, birthday, sex, email, phone, password) {
    return function (dispatch, getState) {
        ajaxSaveProfile(dispatch, login, photo, surname, name, midname, birthday, sex, email, phone, password);
    }
}
export function getOrders() {
    return function (dispatch, getState) {
        const user = getState()[SECURITY].user;
        if (!user) {
            return;
        }
        ajaxGetOrders(dispatch, user.email);
    }
}

export function addOrder(sourcePlace, destinationPlace, vehicle) {
    return function (dispatch, getState) {
        ajaxAddOrder(dispatch, sourcePlace, destinationPlace, vehicle);
    }
}

export function cancelOrder(number) {
    return function (dispatch, getState) {
        ajaxCancelOrder(dispatch, number);
    }
}