import React from 'react';

export default class Sidebar extends React.PureComponent {

    render() {
        return <div className="profile-sidebar">
            <MenuItem
                icon="icon-profile"
                label=""
                active={this.props.path.indexOf("/profile") == 0}
            />
            <div className="filler"></div>
            <MenuItem
                icon="icon-exit"
                label=""
                onClick={this.props.logout}
            />
        </div>
    }
}

class MenuItem extends React.PureComponent {

    render() {
        let className = "menu-item align-center";
        if (this.props.active) {
            className += " active";
        }
        return <div className={className} onClick={this.props.onClick}>
            <div className={`menu-icon svg ${this.props.icon}`}></div>
            <span className="filler">{this.props.label}</span>
        </div>
    }
}