import React from 'react';
import Form from '../form/form';
import Row from '../form/row';
import Cell from '../form/cell';
import Input from '../input/input';
import Checkbox from '../input/checkbox';
import Button from '../button/button';
import { validateEmail, validateMandatory } from '../../services/validation';
import '../../css/login.css';

export default class LoginPage extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            login: this.props.cookies.user || "",
            password: "",
            loginValidation: null,
            passwordValidation: null,
            remember: Boolean(this.props.cookies.user)
        }

        if (this.state.login) {
            this.state.loginValidation = validateEmail(this.state.login);
        }

        this.changeLogin = this.changeLogin.bind(this);
        this.changePassword = this.changePassword.bind(this);
        this.changeRemember = this.changeRemember.bind(this);
        this.login = this.login.bind(this);
        this.signUp = this.signUp.bind(this);
    }

    changeLogin(value) {
        this.clearError();
        const loginValidation = validateEmail(value);
        this.setState(Object.assign({}, this.state, { login: value, loginValidation }));
    }

    changePassword(value) {
        this.clearError();
        const passwordValidation = validateMandatory(value);
        this.setState(Object.assign({}, this.state, { password: value, passwordValidation }));
    }

    changeRemember(value) {
        this.setState(Object.assign({}, this.state, { remember: value }));
    }

    isValid() {
        return this.state.loginValidation && this.state.passwordValidation;
    }

    login() {
        this.props.login({
            user: this.state.login,
            password: this.state.password,
            remember: this.state.remember
        });
    }

    signUp() {
        this.props.changeLocation("/signup");
    }

    clearError() {
        if (!this.props.error) {
            return;
        }
        this.props.clearError();
    }

    render() {
        return <div className="app">
            <div className="app-background">
                <div className="app-container">
                    <div className="app-logo title">
                        <div className="svg logo" />
                        <span>DRON TAXI</span>
                    </div>
                    <Form className="login-form">
                        <Row>
                            <Cell>
                                <span className="title"></span>
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder=" "
                                    password={false}
                                    value={this.state.login}
                                    valid={this.state.loginValidation}
                                    onChange={this.changeLogin}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Input
                                    label=""
                                    placeholder=" "
                                    password={true}
                                    value={this.state.password}
                                    valid={this.state.passwordValidation}
                                    onChange={this.changePassword}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Checkbox
                                    label=""
                                    value={this.state.remember}
                                    onChange={this.changeRemember}
                                />
                            </Cell>
                            <Cell>
                                <Button
                                    label=""
                                    className="btn-blue"
                                    disabled={!this.isValid()}
                                    onClick={this.login}
                                />
                            </Cell>
                        </Row>
                        <Row>
                            <Cell>
                                <Button
                                    label=""
                                    className="btn-green"
                                    onClick={this.signUp}
                                />
                            </Cell>
                        </Row>
                        <LoginError error={this.props.error} />
                    </Form>
                </div>
            </div>
        </div>
    }
}

class LoginError extends React.PureComponent {
    render() {
        if (!this.props.error) {
            return null
        }
        let message = "  ";
        if(this.props.error.errorMessage){
            message = this.props.error.errorMessage;
        }
        return <Row>
            <Cell className="title invalid-text">
                <span>{message}</span>
            </Cell>
        </Row>
    }
}