import React from 'react';
import LockedInput from './locked';
import { validatePasswordLength, validatePasswordUppercase, validatePasswordNumber } from '../../services/validation';

export default class Input extends React.PureComponent {

    constructor(props) {
        super(props);

        this.onChange = this.onChange.bind(this);
    }

    onChange(event) {
        this.props.onChange(event.target.value);
    }

    render() {
        if (this.props.locked) {
            return <LockedInput {...this.props} />
        }
        let className = "form-input input input-text";
        if (this.props.valid === false) {
            className += " invalid";
        }
        if (this.props.className) {
            className += ` ${this.props.className}`;
        }
        let label = this.props.label;
        if (this.props.mandatory) {
            label += " *";
        }
        return [
            this.props.label ? <span key="label" className="input-label">{label}</span> : null,
            <input
                key="input"
                type={this.props.password ? "password" : "text"}
                className={className}
                value={this.props.value}
                placeholder={this.props.placeholder}
                onChange={this.onChange}
            >
            </input>
        ]
    }
}