import React from 'react';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import LockedInput from './locked';

export default class DateInput extends React.PureComponent {

    constructor(props) {
        super(props);

        this.onChange = this.onChange.bind(this);
    }

    onChange(value) {
        this.props.onChange(value);
    }

    render() {
        if (this.props.locked) {
            const value = moment(this.props.value).format("DD.MM.YYYY");
            return <LockedInput {...this.props} value={value} short={true} />
        }
        let className = "form-input input input-text input-date short";
        if (this.props.valid === false) {
            className += " invalid";
        }
        let label = this.props.label;
        if(this.props.mandatory){
            label += " *";
        }
        return [
            this.props.label ? <span key="label" className="input-label">{label}</span> : null,
            <DatePicker
                key="input"
                className={className}
                dateFormat="dd.MM.yyyy"
                selected={this.props.value}
                onChange={this.onChange}
            />
        ]
    }
}