import React from 'react';

import ProfileTab from './tabs/profile';
import OrdersTab from './tabs/orders';
import TripsTab from './tabs/trips';

const PROFILE_TAB = "profile";
const ORDER_TAB = "orders";
const TRIP_TAB = "trips";



export default class ProfileCard extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            activeTab: PROFILE_TAB
        }

        this.changeTab = this.changeTab.bind(this);
    }

    changeTab(tabName) {
        this.setState(Object.assign({}, this.state, { activeTab: tabName }));
    }

    render() {
        return <div className="profile-card">
            <div className="title"> </div>
            <div className="tab-container">
                <TabNav>
                    <Tab tab={PROFILE_TAB} activeTab={this.state.activeTab} changeTab={this.changeTab}> </Tab>
                    <Tab tab={ORDER_TAB} activeTab={this.state.activeTab} changeTab={this.changeTab}> </Tab>
                    <Tab tab={TRIP_TAB} activeTab={this.state.activeTab} changeTab={this.changeTab}> </Tab>
                </TabNav>
                <TabBody
                    user={this.props.user}
                    formState={this.props.formState}
                    formStateError={this.props.formStateError}
                    orders={this.props.orders}
                    ordersError={this.props.ordersError}
                    ordersLoading={this.props.ordersLoading}
                    activeTab={this.state.activeTab}
                    saveProfile={this.props.saveProfile}
                    addOrder={this.props.addOrder}
                    cancelOrder={this.props.cancelOrder}
                />
            </div>
        </div>
    }
}

class TabNav extends React.PureComponent {
    render() {
        return <div className="tab-nav">{this.props.children}</div>
    }
}

class Tab extends React.PureComponent {

    constructor(props) {
        super(props);

        this.changeTab = this.changeTab.bind(this);
    }

    changeTab() {
        this.props.changeTab(this.props.tab);
    }

    render() {
        let className = "tab align-center justify-center";
        if (this.props.activeTab == this.props.tab) {
            className += " active";
        }
        return <div className={className} onClick={this.changeTab}>{this.props.children}</div>
    }
}

class TabBody extends React.PureComponent {
    render() {
        switch (this.props.activeTab) {
            case PROFILE_TAB:
                return <ProfileTab
                    {...this.props.user}
                    formState={this.props.formState}
                    formStateError={this.props.formStateError}
                    save={this.props.saveProfile}
                />;
            case ORDER_TAB:
                return <OrdersTab
                    error={this.props.ordersError}
                    loading={this.props.ordersLoading}
                    data={this.props.orders}
                />;
            case TRIP_TAB:
                return <TripsTab
                    error={this.props.ordersError}
                    loading={this.props.ordersLoading}
                    data={this.props.orders}
                    addOrder={this.props.addOrder}
                    cancelOrder={this.props.cancelOrder}
                />
            default:
                return null;
        }
    }
}