import React from 'react';
import Sidebar from './sidebar';
import ProfileCard from './card';
import '../../css/profile.css';

export default class ProfilePage extends React.PureComponent {

    render() {
        return <div className="app light">
            <div className="app-container">
                <div className="app-logo title">
                    <div className="svg logo" />
                    <span>DRON TAXI</span>
                </div>
                <div className="profile-container">
                    <Sidebar
                        path={this.props.path}
                        logout={this.props.logout}
                    />
                    <ProfileCard
                        user={this.props.user}
                        formState={this.props.formState}
                        formStateError={this.props.formStateError}
                        orders={this.props.orders}
                        ordersError={this.props.ordersError}
                        ordersLoading={this.props.ordersLoading}
                        saveProfile={this.props.saveProfile}
                        addOrder={this.props.addOrder}
                        cancelOrder={this.props.cancelOrder}
                    />
                </div>
            </div>
        </div>
    }
}