import React from 'react';
import LockedInput from './locked';
import Input from './input';
import { validatePasswordLength, validatePasswordUppercase, validatePasswordNumber } from '../../services/validation';

export default class PasswordInput extends React.PureComponent {
    render() {
        if (this.props.locked) {
            return <LockedInput {...this.props} />
        }
        return [
            <Input key="input" type="password" {...this.props} />,
            <PasswordRequires key="requires" valid={this.props.valid} value={this.props.value} />
        ]
    }
}

class PasswordRequires extends React.PureComponent {
    render() {
        if (this.props.valid !== false) {
            return null;
        }
        return [
            validatePasswordUppercase(this.props.value) ? null : <Note key="uppercase" type="danger" >      </Note>,
            validatePasswordNumber(this.props.value) ? null : <Note key="number" type="danger" >   </Note>,
            validatePasswordLength(this.props.value) ? null : <Note key="length" type="danger" >      6 </Note>
        ]
    }
}

class Note extends React.PureComponent {
    render() {
        return <div className={`note-${this.props.type}`}>{this.props.children}</div>
    }
}