import moment from 'moment';
import { VEHICLE_TYPE_BY_CODE, ORDER_CODE_BY_STATE, ORDER_STATE_BY_CODE, VEHICLE_CODE_BY_TYPE } from '../constants/profile';

export function parseOrdersFromServer(orders) {
    const parsedOrders = [];
    if (!Array.isArray(orders)) {
        return parsedOrders;
    }
    for (let order of orders) {
        parsedOrders.push(parseOrderFromServer(order));
    }
    return parsedOrders;
}

export function parseOrderFromServer(order) {
    return {
        number: order.Number,
        status: ORDER_STATE_BY_CODE[order.StatusID],
        sourcePlace: order.StartPoint,
        destinationPlace: order.EndPoint,
        vehicle: VEHICLE_TYPE_BY_CODE[order.Vehicle] || "",
        orderTime: moment(order.DateTime).toDate()
    }
}

export function parseOrderToServer(order) {
    let parsedOrder = {
    }
    if (order.sourcePlace) {
        parsedOrder.StartPoint = order.sourcePlace;
    }
    if (order.destinationPlace) {
        parsedOrder.EndPoint = order.destinationPlace;
    }
    if (order.number) {
        parsedOrder.Number = order.number;
    }
    if (order.status) {
        parsedOrder.StatusID = ORDER_CODE_BY_STATE[order.status];
    }
    if (order.vehicle) {
        parsedOrder.Vehicle = VEHICLE_CODE_BY_TYPE[order.vehicle];
    }
    return parsedOrder;
}