import { LOCATION_SEND_PATH } from "../constants/location";
import { getLocationParams } from "../services/location";


export const initialState = getLocationParams();

function receivePath(state, { path }) {
    const nextState = {...state};
    nextState.path = path;
    return nextState;
}

export default function (state = initialState, action) {
    switch (action.type) {
        case LOCATION_SEND_PATH: return receivePath(state, action.payload);
        default: return state;
    }
}