import { LOCATION_SEND_PATH } from "../constants/location";

/*****************
 * Plain actions *
 *****************/
export function sendLocation(path) {
    return {
        type: LOCATION_SEND_PATH,
        payload: {
            path: path
        }
    }
}

/***********
 * Actions *
 ***********/
export function changeLocation(path) {
    return function (dispatch, getState) {
        window.history.pushState("", "", path);
        dispatch(sendLocation(path));
    }
}