import { SECURITY_SEND_LOGIN_ERROR, SECURITY_SEND_LOGIN_INFO, SECURITY_SEND_SIGN_UP_ERROR, SECURITY_SEND_SIGN_UP_INFO, SECURITY_SEND_LOGOUT, SECURITY_SEND_COOKIES } from "../constants/security";
import { getSecurityCookies } from "../services/security";
import { PROFILE_SEND_SAVE_PROFILE_SUCCESS } from "../constants/profile";

export const initialState = {
    loggedIn: null,
    loginError: false,
    signUpError: null,
    user: null,
    cookies: getSecurityCookies()
};

function receiveLoginError(state, { error }) {
    const nextState = { ...state };
    nextState.loggedIn = false;
    nextState.loginError = error;
    nextState.user = null;
    return nextState;
}

function receiveLoginInfo(state, { user }) {
    const nextState = { ...state };
    nextState.loggedIn = true;
    nextState.loginError = false;
    nextState.user = user;
    return nextState;
}

function receiveLogout(state) {
    let nextState = { ...state };
    nextState.loggedIn = false;
    nextState.loginError = false;
    nextState.user = null;
    if (nextState.cookies.uuid) {
        nextState = receiveCookies(nextState, { uuid: null });
    }
    return nextState;
}

function receiveSignUpError(state, { error }) {
    const nextState = { ...state };
    nextState.signUpError = {
        errorCode: error.errorCode,
        errorMessage: error.errorMessage
    };
    return nextState;
}

function receiveSignUpInfo(state, { user }) {
    const nextState = { ...state };
    nextState.loggedIn = true;
    nextState.signUpError = null;
    nextState.user = user;
    return nextState;
}

function receiveCookies(state, cookies) {
    const nextState = { ...state };
    nextState.cookies = { ...nextState.cookies, ...cookies };
    return nextState;
}

function receiveSaveProfileSuccess(state, { user }) {
    const nextState = { ...state };
    nextState.user = user;
    return nextState;
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SECURITY_SEND_LOGIN_ERROR: return receiveLoginError(state, action.payload);
        case SECURITY_SEND_LOGIN_INFO: return receiveLoginInfo(state, action.payload);
        case SECURITY_SEND_LOGOUT: return receiveLogout(state, action.payload);
        case SECURITY_SEND_SIGN_UP_ERROR: return receiveSignUpError(state, action.payload);
        case SECURITY_SEND_SIGN_UP_INFO: return receiveSignUpInfo(state, action.payload);
        case SECURITY_SEND_COOKIES: return receiveCookies(state, action.payload);
        case PROFILE_SEND_SAVE_PROFILE_SUCCESS: return receiveSaveProfileSuccess(state, action.payload);
        default: return state;
    }
}