import React from 'react';
import ReactDOM from 'react-dom';
import { Provider, connect } from 'react-redux'
import { createStore, compose, applyMiddleware } from 'redux'
import thunk from 'redux-thunk';
import * as serviceWorker from './serviceWorker';
import reducers from './reducers';

import { LOCATION } from './constants/location';
import { SECURITY } from './constants/security';
import { PROFILE } from './constants/profile';

import { changeLocation } from './actions/location';
import { login, signUp, sendLoginError, sendSignUpError, logout, autologin } from './actions/security';

import { initializeLocation } from './services/location';

import App from './App';
import { saveProfile, addOrder, cancelOrder } from './actions/profile';

const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(reducers, composeEnhancer(applyMiddleware(thunk)));
initializeLocation(store.dispatch);

const ConnectedApp = connect((globalState) => {
  return {
    path: globalState[LOCATION].path,
    security: globalState[SECURITY],
    profile: globalState[PROFILE]
  }
}, (dispatch) => {
  return {
    changeLocation: function (path) {
      dispatch(changeLocation(path));
    },
    autologin: function () {
      dispatch(autologin());
    },
    login: function ({ user, password, remember }) {
      dispatch(login(user, password, remember));
    },
    logout: function () {
      dispatch(logout());
    },
    clearLoginError: function () {
      dispatch(sendLoginError(false));
    },
    signUp: function ({ surname, name, midname, email, password }) {
      dispatch(signUp(surname, name, midname, email, password));
    },
    clearSignUpError: function () {
      dispatch(sendSignUpError(null));
    },
    saveProfile: function ({ login, photo, surname, name, midname, birthday, sex, email, phone, password }) {
      dispatch(saveProfile(login, photo, surname, name, midname, birthday, sex, email, phone, password));
    },
    addOrder: function ({ sourcePlace, destinationPlace, vehicle }) {
      dispatch(addOrder(sourcePlace, destinationPlace, vehicle));
    },
    cancelOrder: function ({ number }) {
      dispatch(cancelOrder(number));
    }
  }
})(App)

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <ConnectedApp />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
