import moment from 'moment';
import { formatPhone, validatePassword } from "./validation";

export function getSecurityCookies() {
    const cookies = {
        uuid: getCookie("uuid"),
        user: getCookie("user")
    };
    return cookies;
}

export function uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

export function getCookie(cookieName) {
    var results = document.cookie.match('(^|;) ?' + cookieName + '=([^;]*)(;|$)');

    if (results)
        return (unescape(results[2]));
    else
        return null;
}

export function setCookie(name, value, expDate, path, domain, secure) {
    var cookie_string = name + "=" + escape(value);
    if (expDate) {
        cookie_string += "; expires=" + expDate.toGMTString();
    }
    if (path) {
        cookie_string += "; path=" + escape(path);
    }
    if (domain) {
        cookie_string += "; domain=" + escape(domain);
    }
    if (secure) {
        cookie_string += "; secure";
    }
    document.cookie = cookie_string;
}

export function parseUserFromServer(user) {
    return {
        photo: user.Image || null,
        surname: user.SurName,
        name: user.FirstName,
        midname: user.MidName,
        birthday: user.BirthDay ? moment(user.BirthDay, "YYYY-MM-DD").toDate() : new Date(),
        sex: typeof user.Sex != "undefined" ? parseInt(user.Sex) : null,
        email: user.EMail,
        phone: formatPhone(user.Phone)
    }
}

export function parseUserToServer(user) {
    let parsedUser = {}
    if (user.photo) {
        parsedUser.Image = user.photo;
    }
    if (user.surname) {
        parsedUser.SurName = user.surname;
    }
    if (user.name) {
        parsedUser.FirstName = user.name;
    }
    if (user.midname) {
        parsedUser.MidName = user.midname;
    }
    if (user.birthday) {
        parsedUser.BirthDay = moment(user.birthday).format("YYYY-MM-DD");
    }
    if (typeof user.sex != "undefined") {
        parsedUser.Sex = user.sex.toString();
    }
    if (user.email) {
        parsedUser.EMail = user.email;
    }
    if (typeof user.phone != "undefined") {
        parsedUser.Phone = user.phone;
    }
    if (user.password != "*****" && validatePassword(user.password)) {
        parsedUser.Password = user.password;
    }
    if (user.login) {
        parsedUser.Login = user.login;
    }
    return parsedUser;
}