import React from 'react';
import '../../css/notfound.css';

export default class NotFound extends React.PureComponent {

    constructor(props) {
        super(props);

        this.goBack = this.goBack.bind(this);
        this.goHome = this.goHome.bind(this);
    }

    goBack() {
        window.history.back();
    }

    goHome() {
        this.props.changeLocation("/");
    }

    render() {
        return <div className="app">
            <div className="app-background not-found-page">
                <div className="app-container not-found-page">
                    <div className="app-logo title">
                        <div className="svg logo" />
                        <span>DRON TAXI</span>
                    </div>
                    <div className="not-found-links">
                        <span className="link" onClick={this.goBack}></span>
                        <span> | </span>
                        <span className="link" onClick={this.goHome}> </span>
                    </div>
                    <div className="not-found-title">
                        <span className="not-found-icon">?</span>
                        <span>   "{this.props.path}"  </span>
                    </div>
                </div>
            </div>
        </div>
    }
}