import React from 'react';
import Select from 'react-select';
import Row from '../../form/row';
import Input from '../../input/input';
import Table from '../../table/table';
import Button from '../../button/button';
import { ORDER_STATE, VEHICLE_TYPE } from '../../../constants/profile';
import { validateMandatory } from '../../../services/validation';

export default class TripsTab extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            adding: false
        }

        this.showAdding = this.showAdding.bind(this);
        this.addOrder = this.addOrder.bind(this);
    }

    showAdding() {
        this.setState(Object.assign({}, this.state, { adding: true }));
    }

    addOrder({ sourcePlace, destinationPlace, vehicle }) {
        this.props.addOrder({ sourcePlace, destinationPlace, vehicle });
        this.setState(Object.assign({}, this.state, { adding: false }));
    }

    filterActive(data) {
        const filteredData = [];
        for (let row of data) {
            if (row.status == ORDER_STATE.ACTIVE || row.status == ORDER_STATE.NEW) {
                filteredData.push(row);
            }
        }
        return filteredData;
    }

    render() {
        if (this.props.error) {
            return <ErrorPlaceholder error={this.props.error} />;
        }
        return <div className="tab-body trips-container">
            <ActiveTrips
                data={this.filterActive(this.props.data)}
                showAdding={this.showAdding}
                loading={this.props.loading}
                adding={this.state.adding}
                cancelOrder={this.props.cancelOrder}
            />
            <NewTrip
                visible={this.state.adding}
                addOrder={this.addOrder}
            />
        </div>;
    }
}

class LoadingPlaceholder extends React.PureComponent {
    render() {
        return <div className="tab-body trips-container justify-center align-center">
            <div className="title">...</div>
        </div>
    }
}

class ErrorPlaceholder extends React.PureComponent {
    render() {
        let message = "   ";
        if (this.props.error && this.props.error.errorMessage) {
            message = this.props.error.errorMessage;
        }
        return <div className="tab-body trips-container justify-center align-center">
            <div className="title invalid-text">{message}</div>
        </div>;
    }
}

class ActiveTrips extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            filterFrom: "",
            filterTo: ""
        }

        this.changeInput = this.changeInput.bind(this);

        this.columns = [
            {
                field: "sourcePlace",
                label: " "
            },
            {
                field: "destinationPlace",
                label: " "
            },
            {
                field: "orderTime",
                label: " ",
                format: "dateTime"
            },
            {
                field: "status",
                label: " "
            }
        ];

        this.contextMenuHandler = this.contextMenuHandler.bind(this);
    }

    contextMenuHandler(cell, row) {
        return [
            {
                label: "",
                onClick: this.cancelOrder.bind(this, row)
            }
        ]
    }

    cancelOrder(row) {
        this.props.cancelOrder({ number: row.number });
    }

    changeInput(property, value) {
        const nextState = Object.assign({}, this.state);
        nextState[property] = value;
        this.setState(nextState);
    }

    filterData(data) {
        if (!this.state.filterFrom && !this.state.filterTo) {
            return data;
        }
        const filteredData = [];
        const lowercaseFrom = this.state.filterFrom.toLowerCase();
        const lowercaseTo = this.state.filterTo.toLowerCase();
        for (let row of data) {
            if (this.state.filterFrom
                && row.sourcePlace
                && row.sourcePlace.toLowerCase().indexOf(lowercaseFrom) != -1) {
                filteredData.push(row);
                continue;
            }
            if (this.state.filterTo
                && row.destinationPlace
                && row.destinationPlace.toLowerCase().indexOf(lowercaseTo) != -1) {
                filteredData.push(row);
                continue;
            }
        }
        return filteredData;
    }

    render() {
        return <div className="trips-active">
            <div className="title"> </div>
            <Filter
                fromPlace={this.state.filterFrom}
                toPlace={this.state.filterTo}
                changeFrom={this.changeInput.bind(this, "filterFrom")}
                changeTo={this.changeInput.bind(this, "filterTo")}
            />
            {this.props.loading ? <LoadingPlaceholder /> : <Table
                columns={this.columns}
                data={this.filterData(this.props.data)}
                contextMenu={this.contextMenuHandler}
            />}
            {!this.props.adding && <Row className="justify-right" style={{ marginTop: "20px" }}>
                <Button
                    className={"btn-green btn-edit"}
                    label=" "
                    onClick={this.props.showAdding}
                />
            </Row>}
        </div>;
    }
}

class Filter extends React.PureComponent {
    render() {
        return <div className="filter">
            <div className="svg icon-filter"></div>
            <Input
                placeholder=" "
                password={false}
                value={this.props.fromPlace}
                onChange={this.props.changeFrom}
            />
            <Input
                placeholder=" "
                password={false}
                value={this.props.toPlace}
                onChange={this.props.changeTo}
            />
        </div>;
    }
}

class NewTrip extends React.PureComponent {

    constructor(props) {
        super(props);

        this.vehicleOptions = [
            { value: VEHICLE_TYPE.ECONOME, label: VEHICLE_TYPE.ECONOME },
            { value: VEHICLE_TYPE.BUISINESS, label: VEHICLE_TYPE.BUISINESS },
            { value: VEHICLE_TYPE.PREMIUM, label: VEHICLE_TYPE.PREMIUM }
        ]

        this.initialState = {
            from: "",
            to: "",
            vehicle: this.vehicleOptions[0],
            validation: {
                from: null,
                to: null
            }
        }

        this.state = this.initialState;

        this.changeFrom = this.changeFrom.bind(this);
        this.changeTo = this.changeTo.bind(this);
        this.changeVehicle = this.changeVehicle.bind(this);
        this.addOrder = this.addOrder.bind(this);
    }

    isValid() {
        return this.state.validation.from && this.state.validation.to;
    }

    changeFrom(value) {
        const nextState = Object.assign({}, this.state);
        nextState.from = value;
        nextState.validation.from = validateMandatory(value);
        this.setState(nextState);
    }

    changeTo(value) {
        const nextState = Object.assign({}, this.state);
        nextState.to = value;
        nextState.validation.to = validateMandatory(value);
        this.setState(nextState);
    }

    changeVehicle(value) {
        const nextState = Object.assign({}, this.state);
        nextState.vehicle = value;
        this.setState(nextState);
        console.log(nextState)
    }

    addOrder() {
        this.props.addOrder({
            sourcePlace: this.state.from,
            destinationPlace: this.state.to,
            vehicle: this.state.vehicle.value
        });
        this.setState(this.initialState);
    }

    render() {
        if (!this.props.visible) {
            return null;
        }
        return <div className="trips-create">
            <div className="title"> </div>
            <Row>
                <Input
                    placeholder=""
                    password={false}
                    value={this.state.from}
                    valid={this.state.validation.from}
                    onChange={this.changeFrom}
                />
            </Row>
            <Row>
                <Input
                    placeholder=""
                    password={false}
                    value={this.state.to}
                    valid={this.state.validation.to}
                    onChange={this.changeTo}
                />
            </Row>
            <Row>
                <Select
                    className="select"
                    value={this.state.vehicle}
                    onChange={this.changeVehicle}
                    options={this.vehicleOptions}
                />
            </Row>
            <div className="filler"></div>
            <Row>
                <Button
                    className={"btn-green btn-edit"}
                    label=""
                    disabled={!this.isValid()}
                    onClick={this.addOrder}
                />
            </Row>
        </div>;
    }
}