import React from 'react';
import Form from '../form/form';
import Row from '../form/row';
import Cell from '../form/cell';
import Input from '../input/input';
import Button from '../button/button';
import { SIGN_UP_ERROR } from '../../constants/security';
import { validateMandatory, validateEmail, validatePassword } from '../../services/validation';
import '../../css/signup.css';
import PasswordInput from '../input/password';

export default class SignUpPage extends React.PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            surname: "",
            name: "",
            midname: "",
            email: "",
            password: "",
            passwordConfirm: "",
            surnameValidation: null,
            nameValidation: null,
            emailValidation: null,
            passwordValidation: null,
            passwordConfirmValidation: null
        }

        this.changeInput = this.changeInput.bind(this);
        this.changeEmail = this.changeEmail.bind(this);
        this.changePassword = this.changePassword.bind(this);
        this.changePasswordConfirm = this.changePasswordConfirm.bind(this);
        this.signUp = this.signUp.bind(this);
    }

    changeInput(parameter, mandatory, value) {
        this.clearError();
        const nextState = Object.assign({}, this.state);
        nextState[parameter] = value;
        if (mandatory) {
            nextState[parameter + "Validation"] = validateMandatory(value);
        }
        this.setState(nextState);
    }

    changeEmail(value) {
        this.clearError();
        const emailValidation = validateEmail(value);
        this.setState(Object.assign({}, this.state, { email: value, emailValidation }));
    }

    changePassword(value) {
        this.clearError();
        const passwordValidation = validatePassword(value);
        const passwordConfirmValidation = this.validatePasswordConfirm(this.state.passwordConfirm, value);
        this.setState(Object.assign({}, this.state, { password: value, passwordValidation, passwordConfirmValidation }));
    }

    changePasswordConfirm(value) {
        const passwordConfirmValidation = this.validatePasswordConfirm(value);
        this.setState(Object.assign({}, this.state, { passwordConfirm: value, passwordConfirmValidation }));
    }

    clearError() {
        if (!this.props.error) {
            return;
        }
        this.props.clearError();
    }

    validatePasswordConfirm(value, password = this.state.password) {
        return value === password;
    }

    isValid() {
        return this.state.surnameValidation
            && this.state.nameValidation
            && this.state.emailValidation
            && this.state.passwordValidation
            && this.state.passwordConfirmValidation;
    }

    signUp() {
        this.props.signUp({
            surname: this.state.surname,
            name: this.state.name,
            midname: this.state.midname,
            email: this.state.email,
            password: this.state.password
        });
    }

    componentDidUpdate(prevProps) {
        if (this.props.error != prevProps.error && this.props.error == SIGN_UP_ERROR.EMAIL_EXIST) {
            this.setState(Object.assign({}, this.state, { emailValidation: false }));
        }
    }

    render() {
        return <div className="app">
            <div className="app-background">
                <div className="app-container">
                    <div className="app-logo title">
                        <div className="svg logo" />
                        <span>DRON TAXI</span>
                    </div>
                    <div className="signup-container align-center justify-center">
                        <Form className="signup-form">
                            <Row>
                                <Cell>
                                    <span className="title"></span>
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Input
                                        label=""
                                        placeholder=""
                                        password={false}
                                        value={this.state.surname}
                                        valid={this.state.surnameValidation}
                                        mandatory={true}
                                        onChange={this.changeInput.bind(this, "surname", true)}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Input
                                        label=""
                                        placeholder=""
                                        password={false}
                                        value={this.state.name}
                                        valid={this.state.nameValidation}
                                        mandatory={true}
                                        onChange={this.changeInput.bind(this, "name", true)}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Input
                                        label=""
                                        placeholder=""
                                        password={false}
                                        value={this.state.midname}
                                        onChange={this.changeInput.bind(this, "midname", false)}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Input
                                        label="Email"
                                        placeholder=""
                                        password={false}
                                        value={this.state.email}
                                        valid={this.state.emailValidation}
                                        mandatory={true}
                                        onChange={this.changeEmail}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <PasswordInput
                                        label=""
                                        placeholder=""
                                        password={true}
                                        value={this.state.password}
                                        valid={this.state.passwordValidation}
                                        mandatory={true}
                                        onChange={this.changePassword}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Input
                                        label=" "
                                        placeholder=""
                                        password={true}
                                        value={this.state.passwordConfirm}
                                        valid={this.state.passwordConfirmValidation}
                                        mandatory={true}
                                        onChange={this.changePasswordConfirm}
                                    />
                                </Cell>
                            </Row>
                            <Row>
                                <Cell>
                                    <Button
                                        label=""
                                        className="btn-green"
                                        disabled={!this.isValid()}
                                        onClick={this.signUp}
                                    />
                                </Cell>
                            </Row>
                            <SignUpError
                                error={this.props.error}
                            />
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    }
}

class SignUpError extends React.PureComponent {
    render() {
        if (!this.props.error) {
            return null
        }
        let errorText = "   ";
        if (this.props.error.errorCode == SIGN_UP_ERROR.ERROR_WITH_MESSAGE) {
            errorText = this.props.error.errorMessage;
        }
        return <Row>
            <Cell className="title invalid-text" style={{ maxWidth: "350px" }}>
                <span>{errorText}</span>
            </Cell>
        </Row>
    }
}