import { SECURITY_SEND_LOGIN_ERROR, SECURITY_SEND_LOGIN_INFO, SECURITY_SEND_SIGN_UP_ERROR, SECURITY_SEND_SIGN_UP_INFO, SECURITY_SEND_LOGOUT, SECURITY_SEND_COOKIES } from "../constants/security";
import { getSecurityCookies } from "../services/security";
import { FORM_STATE, PROFILE_SEND_SAVE_PROFILE_WAIT, PROFILE_SEND_SAVE_PROFILE_ERROR, PROFILE_SEND_SAVE_PROFILE_SUCCESS, PROFILE_SEND_GET_ORDERS_WAIT, PROFILE_SEND_GET_ORDERS_ERROR, PROFILE_SEND_GET_ORDERS_SUCCESS } from "../constants/profile";

export const initialState = {
    formState: FORM_STATE.READY,
    formStateError: null,
    orders: [],
    ordersLoading: null,
    ordersError: null
};

function receiveSaveProfileWait(state) {
    const nextState = { ...state };
    nextState.formState = FORM_STATE.WAIT;
    return nextState;
}

function receiveSaveProfileError(state, { errorMessage }) {
    const nextState = { ...state };
    nextState.formState = FORM_STATE.SAVE_ERROR;
    nextState.formStateError = errorMessage;
    return nextState;
}

function receiveSaveProfileSuccess(state, { user }) {
    const nextState = { ...state };
    nextState.formState = FORM_STATE.READY;
    return nextState;
}

function receiveGetOrdersWait(state) {
    const nextState = { ...state };
    nextState.ordersLoading = true;
    nextState.ordersError = false;
    return nextState;
}

function receiveGetOrdersError(state, { error }) {
    const nextState = { ...state };
    nextState.ordersLoading = false;
    nextState.ordersError = error;
    return nextState;
}

function receiveGetOrdersSuccess(state, { orders }) {
    const nextState = { ...state };
    nextState.ordersLoading = false;
    nextState.ordersError = false;
    nextState.orders = orders;
    return nextState;
}

export default function (state = initialState, action) {
    switch (action.type) {
        case PROFILE_SEND_SAVE_PROFILE_WAIT: return receiveSaveProfileWait(state, action.payload);
        case PROFILE_SEND_SAVE_PROFILE_ERROR: return receiveSaveProfileError(state, action.payload);
        case PROFILE_SEND_SAVE_PROFILE_SUCCESS: return receiveSaveProfileSuccess(state, action.payload);
        case PROFILE_SEND_GET_ORDERS_WAIT: return receiveGetOrdersWait(state, action.payload);
        case PROFILE_SEND_GET_ORDERS_ERROR: return receiveGetOrdersError(state, action.payload);
        case PROFILE_SEND_GET_ORDERS_SUCCESS: return receiveGetOrdersSuccess(state, action.payload);
        default: return state;
    }
}