import React from 'react';
import moment from 'moment';

export default class Table extends React.PureComponent {

    constructor(props) {
        super(props);

        this.initialContextMenu = {
            visible: false,
            position: {
                x: 0,
                y: 0
            },
            items: []
        }

        this.state = {
            contextMenu: this.initialContextMenu
        }

        this.clearContextMenu = this.clearContextMenu.bind(this);
        this.onContextMenu = this.onContextMenu.bind(this);
    }

    clearContextMenu() {
        this.setState(Object.assign({}, this.state, { contextMenu: this.initialContextMenu }));
    }

    removeListener(listener) {
        document.removeEventListener("click", listener);
    }

    onContextMenu(cell, row, reactEvent) {
        if (!this.props.contextMenu) {
            return null;
        }
        reactEvent.preventDefault();
        reactEvent.stopPropagation();
        const contextMenuState = {
            visible: true,
            position: {
                x: reactEvent.clientX,
                y: reactEvent.clientY
            },
            items: this.props.contextMenu(cell, row)
        }
        this.setState(Object.assign({}, this.state, { contextMenu: contextMenuState }));
    }

    componentDidMount() {
        this.listener = document.addEventListener("click", () => {
            this.clearContextMenu();
        });
    }

    componentWillUnmount() {
        if (this.listener) {
            this.removeListener(this.listener);
        }
    }

    render() {
        let className = "table";
        if (this.props.className) {
            className += ` ${this.props.className}`;
        }
        return [
            <ContextMenu
                key="contextMenu"
                visible={this.state.contextMenu.visible}
                position={this.state.contextMenu.position}
                items={this.state.contextMenu.items}
                clearMenu={this.clearContextMenu}
            />,
            <table key="table" className={className}>
                <thead className="table-header">
                    <tr>
                        {this.props.columns.map((column, i) => <HeaderCell key={i} label={column.label} />)}
                    </tr>
                </thead>
                <tbody className="table-body">
                    {this.props.data.map((row, i) => <DataRow
                        key={i}
                        row={row}
                        columns={this.props.columns}
                        onContextMenu={this.onContextMenu}
                    />)}
                </tbody>
            </table>
        ]
    }
}

class ContextMenu extends React.PureComponent {

    itemClick(callback, reactEvent) {
        callback();
        this.props.clearMenu();
    }

    render() {
        if(!this.props.visible){
            return null;
        }
        return <div className="context-menu" style={{ left: this.props.position.x, top: this.props.position.y }}>
            {this.props.items.map((item, i) => <ContextMenuItem key={i} label={item.label} onClick={this.itemClick.bind(this, item.onClick)} />)}
        </div>;
    }
}

class ContextMenuItem extends React.PureComponent {
    render() {
        return <div className="context-menu-item" onClick={this.props.onClick}>{this.props.label}</div>;
    }
}

class HeaderCell extends React.PureComponent {
    render() {
        return <th className="cell">{this.props.label}</th>;
    }
}

class DataRow extends React.PureComponent {
    render() {
        return <tr className="row">
            {this.props.columns.map((column, i) => <DataCell
                key={i}
                row={this.props.row}
                column={column}
                onContextMenu={this.props.onContextMenu}
            />)}
        </tr>;
    }
}

class DataCell extends React.PureComponent {
    constructor(props) {
        super(props);

        this.printHandler = this.printHandler.bind(this);
        this.onContextMenu = this.onContextMenu.bind(this);
    }

    printHandler(cell, row) {
        if (this.props.column.format == "dateTime") {
            return moment(cell).format("DD.MM.YYYY hh:mm:ss");
        }
        if (this.props.column.format == "currency") {
            return cell + " ";
        }
        return cell;
    }

    onContextMenu(reactEvent) {
        this.props.onContextMenu(this.props.row[this.props.column.field], this.props.row, reactEvent);
    }

    render() {
        let printHandler = this.printHandler;
        if (this.props.column.printHandler) {
            printHandler = this.props.column.printHandler;
        }
        return <td className="cell" onContextMenu={this.onContextMenu}>
            {printHandler(this.props.row[this.props.column.field], this.props.row)}
        </td>;
    }
}